.contact {
  display: flex;
  justify-content: space-between;

  &__info {
    display: flex;
    justify-content: space-between;

    &--right {
      padding-right: 30px;
      margin-left: 108px;
      @media screen and (max-width: 800px) {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .contact {
    flex-direction: column;

    &__info {
      flex-direction: column;
    }
  }
}
